import React from 'react'
import {Home, LiveTv, PersonOutlined, Search, VideoLibrary, FlashOn} from "@mui/icons-material"
import "./Header.css";
import { Grid } from '@mui/material';

const Header = () => {
  return (
    <Grid container>
    <Grid item xs={12} sm={6}md={10}>
    <div className="header">
        <div className="header__icons">
        <Grid item xs={2} sm={2} md={2}>
            <div className="header__icon header__icon--active">
                <Home/>
                <p>Home</p>
            </div>
            </Grid>
            <Grid item xs={2} sm={2} md={2}>
            <div className="header__icon">
                <LiveTv/>
                <p>Verified</p>
            </div>
            </Grid>
            <Grid item xs={2} sm={2}md={2}>
            <div className="header__icon">
                <FlashOn/>
                <p>Trending</p>
            </div>
            </Grid>
            <Grid item xs={2} sm={2}md={2}>
            <div className="header__icon">
                <VideoLibrary/>
                <p>Collections</p>
            </div>
            </Grid>
            <Grid item xs={2} sm={2} md={2}>
            <div className="header__icon">
                <Search/>
                <p>Search</p>
            </div>
            </Grid>
            <Grid item xs={2} sm={2}md={2}>
            <div className="header__icon">
                <PersonOutlined/>
                <p>Account</p>
            </div>
            </Grid>
        </div>
        </div>
        </Grid>
  <Grid item xs={12} sm={6} md={2} className="header">
    <h1><span>l</span>ive<br/><span>X</span>stream</h1>
  </Grid>  
  </Grid>
    
  )
}

export default Header
